import React, { memo } from "react";

import { EventCardWrapper } from "./style";

import KTEventCardHeader from "./event-card-header";
import KTEventCardContent from "./event-card-content";
import { calculateValue } from "./utils";

const KTEventCard = memo((props) => {
  const { cardWidth } = props;
  const { addr1, imageName } = props.item;

  const wrapperGap = calculateValue(cardWidth, 12);
  return (
    <EventCardWrapper style={{ gap: wrapperGap }}>
      <KTEventCardHeader address={addr1} cardWidth={cardWidth} />
      <KTEventCardContent imageName={imageName} cardWidth={cardWidth} item={props.item}/>
    </EventCardWrapper>
  );
});

export default KTEventCard;
