import React, { memo } from "react";

import { EventCardHeaderWrapper } from "./style";
import { ReactComponent as AddressIcon } from "@/assets/img/svg/icon-address.svg";
import { calculateValue } from "../utils";

const KTEventCardHeader = memo(({ address, cardWidth }) => {
  function cValue(value) {
    return calculateValue(cardWidth, value);
  }
  return (
    <EventCardHeaderWrapper
      gap={cValue(20)}
      padding={cValue(10)}
      minHeight={cValue(32)}
      fontSize={cValue(15)}
    >
      <AddressIcon
        className="icon"
        style={{
          width: cValue(26),
          height: cValue(26),
        }}
      />
      <div className="title">{address}</div>
    </EventCardHeaderWrapper>
  );
});

export default KTEventCardHeader;
