import React, { memo } from "react";
import KTEventCard from "../event-card";
import KTEventCell from "../event-cell";
import { EventsListViewWrapper } from "./style";
import { LAYOUT_MODE_CARD } from "../../common/constants";
import KTEventCellSmall from "../event-cell-small";

const KTEvemtsListView = ({ events, layoutMode, cardWidth, windowWidth }) => {
  if (layoutMode === LAYOUT_MODE_CARD) {
    const items = events.map((item, index) => {
      return <KTEventCard key={index} item={item} cardWidth={cardWidth} />;
    });
    return (
      <EventsListViewWrapper
        style={{
          gridTemplateColumns: `repeat(auto-fill, ${cardWidth}px)`,
          rowGap: "60px",
        }}
      >
        {items}
      </EventsListViewWrapper>
    );
  } else {
    const items = events.map((item) => {
      if (windowWidth > 1024) {
        return (
          <KTEventCell key={item.id} item={item} windowWidth={windowWidth} />
        );
      } else {
        return (
          <KTEventCellSmall
            key={item.id}
            item={item}
            windowWidth={windowWidth}
          />
        );
      }
    });
    return (
      <EventsListViewWrapper
        style={{
          gridTemplateColumns: "1fr",
          rowGap: "0",
        }}
      >
        {items}
      </EventsListViewWrapper>
    );
  }
};

export default memo(KTEvemtsListView);
