import React, { memo, useState } from "react";

import {
  EventCardBackgroundWrapper,
  EventCardContentWrapper,
  EventCardContentOperationBar,
  EventCardContentDateBar,
} from "./style";
import { ReactComponent as LikeIcon } from "@/assets/img/svg/icon-like.svg";
import { ReactComponent as ShareIcon } from "@/assets/img/svg/icon-share.svg";
import { calculateValue } from "../utils";
import classNames from "classnames";

const KTEventCardContent = memo(({ cardWidth, item, imageName }) => {
  function cValue(value) {
    return calculateValue(cardWidth, value);
  }

  const [isOnMouseOver, setIsOnMouseOver] = useState(false);
  const [isLike, setIsLike] = useState(false);
  function onMouseOver(item) {
    setIsOnMouseOver(true);
  }
  function onMouseOut(item) {
    setIsOnMouseOver(false);
  }

  function onClickLike() {
    setIsLike(!isLike);
  }

  return (
    <EventCardBackgroundWrapper
      onMouseOver={(item) => onMouseOver(item)}
      onMouseOut={(item) => onMouseOut(item)}
      style={{
        backgroundImage:
          "url(" + require(`../../../assets/img/event-list/${imageName}`) + ")",
      }}
    >
      <EventCardContentWrapper
        padding={cValue(24)}
        titleFontSize={cValue(16)}
        titleLineHeight={cValue(20)}
        titleMarginTop={cValue(15)}
        titleMinHeight={cValue(88)}
        buttonMarginTop={cValue(20)}
        buttonWidth={cValue(140)}
        buttonHeight={cValue(30)}
        buttonFontSize={cValue(15)}
      >
        <EventCardContentOperationBar>
          <LikeIcon
            className="like"
            style={{
              width: cValue(26),
              height: cValue(22),
              fill: isLike ? "#E21737" : "",
            }}
            onClick={onClickLike}
          />
          <ShareIcon
            className="share"
            style={{ width: cValue(22), height: cValue(22) }}
          />
        </EventCardContentOperationBar>
        <EventCardContentDateBar>
          <div className="date">20 Aug. 2023</div>
          <div className="time">6:30 pm,</div>
          <div className="week"> Wed.</div>
        </EventCardContentDateBar>
        <div className="title">
          NFL American Football Pre-season: Kansas City Chiefs at team city and
          name, 4 Lines Max...
        </div>
        <div
          className={classNames("buyTicketButton", {
            buyTicketButtonHover: isOnMouseOver,
          })}
        >
          Buy Ticket
        </div>
      </EventCardContentWrapper>
    </EventCardBackgroundWrapper>
  );
});

export default KTEventCardContent;
